import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Autocomplete from '@mui/material/Autocomplete';

const dispatchedEvents = [];

const SrsMuiAutocomplete = function (props, ref) {
  let componentProps = {...props, theme: props.muiTheme};
  return (
    <Autocomplete {...componentProps}>
      <slot />
    </Autocomplete>
  );
}

const styles = `
  /* Custom styles for the Autocomplete component */
`;

const props = [
  {
    name: "id",
    defaultValue: "",
    type: "string",
  },
  {
    name: "options",
    defaultValue: [],
    type: "object",
  },
  {
    name: "value",
    defaultValue: null,
    type: "string",
  },
  {
    name: "onChange",
    defaultValue: null,
    type: "function",
  },
  {
    name: "renderInput",
    defaultValue: null,
    type: "function",
  },
  {
    name: "className",
    defaultValue: "",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object",
  },
  // Add other props specific to Autocomplete if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(React.forwardRef(SrsMuiAutocomplete), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };
